#add-sample #breadcrumb li a[data-v-400aeefe] {
  color: #FFF;
  display: block;
  background: #3498db;
  text-decoration: none;
  position: relative;
  height: 40px;
  line-height: 40px;
  padding: 0 10px 0 5px;
  text-align: center;
  margin-right: 23px;
}
#add-sample #breadcrumb li:first-child a[data-v-400aeefe] {
  padding-left: 15px;
@include border-radius(4 px 0 0 4 px);
}
#add-sample #breadcrumb li:first-child a[data-v-400aeefe]:before {
  border: none;
}
#add-sample #breadcrumb li:last-child a[data-v-400aeefe] {
  padding-right: 15px;
@include border-radius(0 4 px 4 px 0);
}
#add-sample #breadcrumb li:last-child a[data-v-400aeefe]:after {
  border: none;
}
#add-sample #breadcrumb li a.in[data-v-400aeefe] {
  background-color: #1abc9c;
}
#add-sample #breadcrumb li a.in[data-v-400aeefe]:before {
  border-color: #1abc9c;
  border-left-color: transparent;
}
#add-sample #breadcrumb li a.in[data-v-400aeefe]:after {
  border-left-color: #1abc9c;
}
#add-sample #breadcrumb li a[data-v-400aeefe]:before,
#add-sample #breadcrumb li a[data-v-400aeefe]:after {
  content: "";
  position: absolute;
  top: 0;
  border: 0 solid #3498db;
  border-width: 20px 10px;
  width: 0;
  height: 0;
}
#add-sample #breadcrumb li a[data-v-400aeefe]:before {
  left: -20px;
  border-left-color: transparent;
}
#add-sample #breadcrumb li a[data-v-400aeefe]:after {
  left: 100%;
  border-color: transparent;
  border-left-color: #3498db;
}
#add-sample #breadcrumb li a[data-v-400aeefe]:hover {
  background-color: #1abc9c;
}
#add-sample #breadcrumb li a[data-v-400aeefe]:hover:before {
  border-color: #1abc9c;
  border-left-color: transparent;
}
#add-sample #breadcrumb li a[data-v-400aeefe]:hover:after {
  border-left-color: #1abc9c;
}
#add-sample #breadcrumb li a[data-v-400aeefe]:active {
  background-color: #16a085;
}
#add-sample #breadcrumb li a[data-v-400aeefe]:active:before {
  border-color: #16a085;
  border-left-color: transparent;
}
#add-sample #breadcrumb li a[data-v-400aeefe]:active:after {
  border-left-color: #16a085;
}



