
@import "components/global/css/common.css";
.bc {
  background: #fff;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  font-weight: bold;
}
html {
  min-width: 1160px;
  background: linear-gradient(to bottom, #f0f0f0, #ffffff);
  min-height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}
html body {
  background-color: #f5f5f5;
  /*min-height: 100%;*/
  min-height: 100vh;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: "Microsoft YaHei", "微软雅黑" !important;
}
html body #app {
  /*min-height: 50px;*/
  min-height: 100vh;
  /*overflow-x: hidden;*/
  position: relative;
}
html body #app .no-data {
  color: red;
  margin: 20px;
}
html body #app .router-content {
  width: 100%;
  min-height: calc(100vh - 50px);
  background-size: 100% 100%;
}
html body #app .router-content .p-div {
  padding: 30px 60px;
}
html body #app .router-content .data-content {
  min-height: calc(100vh - 50px);
}
html body #app .router-content .data-content .left-ul {
  display: inline-block;
  width: 251px;
  background-color: #f5f5f5;
  min-height: calc(100vh - 50px);
  margin: 0;
  padding: 0;
  color: #313131;
}
html body #app .router-content .data-content .left-ul li {
  cursor: pointer;
}
html body #app .router-content .data-content .left-ul li.active {
  background-color: #fff;
}
html body #app .router-content .data-content .left-ul li.active .father {
  border-right: 1px solid #0076c0;
  color: #0e7dc3;
}
html body #app .router-content .data-content .left-ul li.active .father .triangle {
  opacity: 1;
}
html body #app .router-content .data-content .left-ul .father {
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #d3d4d4;
  border-top: 1px solid #fbfbfb;
  font-weight: bold;
}
html body #app .router-content .data-content .left-ul .father .img {
  float: left;
  width: 16px;
  height: 25px;
  margin: 6px 12px 0 20px;
}
html body #app .router-content .data-content .left-ul .father img {
  float: right;
  margin: 16px 20px 0 0;
}
html body #app .router-content .data-content .left-ul .father .triangle {
  float: right;
  width: 0;
  height: 0;
  margin-top: 4px;
  border-top: 15px solid transparent;
  border-right: 18px solid #0076c0;
  border-bottom: 15px solid transparent;
  opacity: 0;
}
html body #app .router-content .data-content .left-ul .children {
  /*display: none;*/
  border-top: 1px solid #fbfbfb;
  border-bottom: 1px solid #d3d4d4;
  font-size: 13px;
}
html body #app .router-content .data-content .left-ul .children a {
  height: 25px;
  line-height: 25px;
  color: inherit;
  padding-left: 23px;
}
html body #app .router-content .data-content .left-ul .children a.active,
html body #app .router-content .data-content .left-ul .children a:hover,
html body #app .router-content .data-content .left-ul .children .router-link-active,
html body #app .router-content .data-content .left-ul .children .router-link-active:hover {
  color: #0e7dc3;
  background-color: #dceef9;
}
html body #app .router-content .data-content .right-content {
  display: inline-block;
  min-height: calc(100vh - 50px);
  min-width: calc(100vw - 275px);
  max-width: calc(100vw - 275px);
  vertical-align: top;
  padding: 20px 10px;
  border-left: 1px solid #d3d4d4;
  margin-left: -4px;
  background: url("../static/img/html-bc.png") repeat center;
  background-size: 100% 100%;
  background-position: 0 0;
}
html body #app .router-content .data-content .drop-down > .title {
  background: #fff;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  font-weight: bold;
  font-size: 16px;
  padding: 10px 20px;
}
html body #app .router-content .data-content .drop-down > .content-one {
  background: #fff;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  font-weight: bold;
  background-color: #fff;
  margin: 20px 0;
  padding: 10px 0;
}
html body #app .router-content .data-content .drop-down > .content-one .header {
  position: relative;
  height: 42px;
  line-height: 42px;
  padding: 0 15px 0 35px;
  color: #333;
  cursor: pointer;
}
html body #app .router-content .data-content .drop-down > .content-one .header .fa {
  color: #0e7dc3;
  margin-right: 15px;
  width: 14px;
  height: 14px;
}
html body #app .router-content .data-content .drop-down > .content-one .content {
  padding: 15px 35px 15px;
  font-size: 12px;
}
html body #app .router-content .data-content .drop-down > .content-one .left-right {
  overflow: hidden;
}
html body #app .router-content .data-content .drop-down > .content-one .left-right .left,
html body #app .router-content .data-content .drop-down > .content-one .left-right .right {
  float: left;
  width: 48%;
}
html body #app .router-content .data-content .drop-down > .content-one .left-right .left .title,
html body #app .router-content .data-content .drop-down > .content-one .left-right .right .title {
  margin-bottom: 15px;
  padding: 0 10px;
  line-height: 40px;
  height: 40px;
  background: #e4ecea;
  border-left: 3px solid #27a482;
  color: #333;
  overflow: hidden;
  font-size: 14px;
}
html body #app .router-content .data-content .drop-down > .content-one .left-right .left ul,
html body #app .router-content .data-content .drop-down > .content-one .left-right .right ul {
  margin-left: 12px;
}
html body #app .router-content .data-content .drop-down > .content-one .left-right .left ul li,
html body #app .router-content .data-content .drop-down > .content-one .left-right .right ul li {
  height: 33px;
  line-height: 33px;
  border-bottom: 1px dashed #ddd;
}
html body #app .router-content .data-content .drop-down > .content-one .left-right .left {
  margin-right: 2%;
}



